* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.messenger {
  width: 100%;
  height: 100vh;
  font-family: Arial, sans-serif;
  font-size: 14px;
  display: flex;
}

.messenger__header {
  max-width: calc(100vw - 260px);
  height: 44px;
  background-color: #ffecdb;
  align-items: center;
  line-height: 44px;
  display: flex;
  position: relative;
}

.messenger__left-block {
  min-width: 260px;
  background-color: #ededed;
}

.left-block__header {
  min-width: 260px;
}

.button-small {
  width: 50px;
  min-width: 50px;
  height: 24px;
  background-color: #ffc8b0;
  border: 1px solid #ffc8b0;
  border-radius: 4px;
  margin: 0 10px;
  line-height: 24px;
}

.button-small:hover {
  background-color: #fff;
  border: 1px solid #000;
}

.input-search-chat {
  min-width: 160px;
  height: 24px;
  color: #a6a6a6;
  background-color: #fff;
  border: 1px solid #a6a6a6;
  border-radius: 4px;
  margin: 0 10px;
  padding: 0 7px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
}

.input-search-chat:focus {
  color: #4a4a4a;
  border: 1px solid #4a4a4a;
  outline: none;
}

.chats-block {
  max-height: calc(100vh - 44px);
  flex-direction: column;
  display: flex;
  overflow: auto;
}

.messenger__main-block {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.main-block__header {
  min-width: 360px;
}

.main-block__header-title {
  min-width: calc(100% - 230px);
  max-height: 44px;
  color: #4a4a4a;
  margin: 0 10px;
  font-weight: 700;
  overflow: visible;
}

.button-close {
  background-color: silver;
  border: 1px solid silver;
}

.main-block__chat-block {
  height: calc(100% - 94px);
  overflow: auto;
}

.chat-block__chat {
  width: 420px;
  flex-direction: column;
  margin-left: 44px;
  display: flex;
}

.chat__date {
  text-align: center;
  margin: 4px;
}

.chat__message {
  min-height: 30px;
  margin: 3px 0;
  line-height: 20px;
}

.chat__message_to {
  background-color: #cceafb;
}

.chat__message_from {
  background-color: #ffecdb;
}

.chat__message-inner {
  border-radius: 8px;
  padding: 5px 15px;
  font-size: 14px;
  display: inline-block;
  position: relative;
}

.chat__message-avatar {
  width: 34px;
  height: 34px;
  border: 1px solid #000;
  border-radius: 50%;
  position: absolute;
  bottom: 0;
  left: -40px;
}

.chat__message-avatar_from {
  background-color: #ffc8b0;
}

.chat__message-avatar_to {
  background-color: #5fbbef;
}

.chat__message-time {
  height: 14px;
  color: #fff;
  border-radius: 8px 0;
  padding: 1px 4px 0;
  font-size: 12px;
  line-height: 14px;
  position: relative;
  bottom: -7.2px;
  right: -15px;
}

.chat__message-time_from {
  background-color: #ffb09e;
}

.chat__message-time_to_sent {
  background-color: silver;
}

.chat__message-time_to_recd {
  background-color: #5fbbef;
}

.main-block__send-block {
  height: 50px;
  background-color: silver;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.button-staple {
  width: 30px;
  height: 100%;
  background: url("../../staple.8ded1c9a.svg") 60% no-repeat;
  border: none;
  margin: 0 6px;
}

.button-staple:hover {
  background: url("../../staple-hover.a8e130c3.svg") 60% no-repeat;
}

.input-send {
  min-width: 160px;
  height: 32px;
  color: #4a4a4a;
  background-color: #fff;
  border: 1px solid #a6a6a6;
  border-radius: 5px;
  flex-grow: 1;
  padding: 0 7px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
}

.input-send:focus {
  border: 1px solid #4a4a4a;
  outline: none;
}

.button-send {
  width: 70px;
  cursor: pointer;
  color: #717171;
  background: none;
  border: none;
  font-family: Exo2, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.button-send:hover {
  color: #179eea;
}

